___CSS_LOADER_EXPORT___.push([module.id, "#app,body{background:#fff!important}", "",{"version":3,"sources":["webpack://./resources/js/components/host/Home.vue"],"names":[],"mappings":"AAgmDA,UACA,yBACA","sourcesContent":["<template>\r\n    <div class=\"host-home-main-cont\">\r\n        <div class=\"host-home-left-cont\">\r\n            <h1>Welcome {{ loggedIn.name }}..</h1>\r\n            <div v-if=\"invitation\" class=\"host-banner-cont\">\r\n                <img src=\"/assets/hosthome/host_home.png\" alt=\"\" />\r\n                <div class=\"overlay\"></div>\r\n                <div class=\"host-banner-timer-cont\">\r\n                    <div class=\"img-cont\">\r\n                        <img src=\"/assets/hosthome/Frame5647.png\" alt=\"\" />\r\n                        <img src=\"/assets/hosthome/Frame5647.png\" alt=\"\" />\r\n                    </div>\r\n                    <div class=\"name-cont\">\r\n                        <span>{{ invitation.groomName }}</span>\r\n                        <span>weds</span>\r\n                        <span>{{ invitation.brideName }}</span>\r\n                    </div>\r\n                    <div class=\"date-cont\">\r\n                        <img src=\"/assets/guestInvi/18.png\" alt=\"\" />\r\n                        <span>{{\r\n                            invitation.startDate | moment(\"DD.MM.YYYY\")\r\n                        }}</span>\r\n                        <img src=\"/assets/guestInvi/17.png\" alt=\"\" />\r\n                    </div>\r\n                    <div class=\"guest-banner-timer\">\r\n                        <div class=\"guest-banner-timer-single\">\r\n                            <span></span>\r\n                            <span>days</span>\r\n                        </div>\r\n                        <div class=\"guest-banner-timer-single\">\r\n                            <span></span>\r\n                            <span>hours</span>\r\n                        </div>\r\n                        <div class=\"guest-banner-timer-single\">\r\n                            <span></span>\r\n                            <span>minutes</span>\r\n                        </div>\r\n                        <div class=\"guest-banner-timer-single\">\r\n                            <span></span>\r\n                            <span>seconds</span>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div v-else class=\"no-invi-banner-cont\">\r\n                <div class=\"no-invi-content\">\r\n                    <!-- <h3>Welcome Sandeep..</h3> -->\r\n                    <p>\r\n                        Plan and organize your wedding systematically, Make it\r\n                        memorable with friends and relatives For a unique love\r\n                        story\r\n                    </p>\r\n                    <a :href=\"route('hostinvitations.index')\"\r\n                        >Create your invitation</a\r\n                    >\r\n                </div>\r\n                <img src=\"/assets/hosthome/noinvi.svg\" alt=\"\" />\r\n            </div>\r\n            <div\r\n                v-if=\"screenWidth < 631 && todaySchedule.length > 0\"\r\n                class=\"host-today-task\"\r\n            >\r\n                <h1>Today’s Task</h1>\r\n                <div>\r\n                    <div\r\n                        v-for=\"item in todaySchedule\"\r\n                        :key=\"item.id\"\r\n                        class=\"host-today-task-single\"\r\n                    >\r\n                        <div class=\"host-today-task-single-head\">\r\n                            <span>\r\n                                <span></span>\r\n                                {{ item.taskStatus }}\r\n                            </span>\r\n                            <span>{{ item.category.name }}</span>\r\n                        </div>\r\n                        <div class=\"host-today-task-assign\">\r\n                            Assigned to\r\n                            <span>: {{ item.assignable.name }}</span>\r\n                        </div>\r\n                        <p>\r\n                            {{ item.title }}\r\n                        </p>\r\n                        <div class=\"host-today-schedule\">\r\n                            <span\r\n                                ><img\r\n                                    src=\"/assets/icon-calendar.svg\"\r\n                                    alt=\"\"\r\n                                />{{ item.startDate | moment(\"Do MMMM, YYYY\") }}\r\n                                -\r\n                                {{\r\n                                    item.completionDate\r\n                                        | moment(\"Do MMMM, YYYY\")\r\n                                }}</span\r\n                            >\r\n                            <span>\r\n                                <img src=\"/assets/icon-clock.svg\" alt=\"\" />{{\r\n                                    new Date(\"1970-01-01T\" + item.startTime)\r\n                                        | moment(\"h:mm a\")\r\n                                }}\r\n                                -\r\n                                {{\r\n                                    new Date(\r\n                                        \"1970-01-01T\" + item.completionTime\r\n                                    ) | moment(\"h:mm a\")\r\n                                }}</span\r\n                            >\r\n                            <span\r\n                                ><img src=\"/assets/tag.svg\" alt=\"\" />{{\r\n                                    item.taskPriority\r\n                                }}\r\n                                Priority</span\r\n                            >\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"host-left-second-cont\">\r\n                <div class=\"host-invi-noti-cont\">\r\n                    <div class=\"host-invi-status-cont\">\r\n                        <h1\r\n                            @click=\"\r\n                                show == 'inviStatus'\r\n                                    ? (show = 'null')\r\n                                    : (show = 'inviStatus')\r\n                            \"\r\n                        >\r\n                            Invitation Status\r\n                            <img\r\n                                v-if=\"\r\n                                    screenWidth <= 631 && show != 'inviStatus'\r\n                                \"\r\n                                src=\"/assets/guestDash/chevron-down.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <img\r\n                                v-if=\"\r\n                                    screenWidth <= 631 && show == 'inviStatus'\r\n                                \"\r\n                                src=\"/assets/guestDash/chevron-up.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                        </h1>\r\n                        <div\r\n                            v-show=\"show == 'inviStatus' || screenWidth >= 631\"\r\n                        >\r\n                            <div class=\"invi-status-single\">\r\n                                <span>\r\n                                    <img\r\n                                        src=\"/assets/hosthome/ic_baseline-card-giftcard.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    Total Invitation\r\n                                </span>\r\n                                <span>{{ invitationcount }}</span>\r\n                            </div>\r\n                            <div class=\"invi-status-single\">\r\n                                <span>\r\n                                    <img\r\n                                        src=\"/assets/hosthome/mdi_clipboard-tick-outline.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    Accepted\r\n                                </span>\r\n                                <span>{{ invitationsaccepted }}</span>\r\n                            </div>\r\n                            <div class=\"invi-status-single\">\r\n                                <span>\r\n                                    <img\r\n                                        src=\"/assets/hosthome/carbon_pending.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    Pending\r\n                                </span>\r\n                                <span>{{ invitationspending }}</span>\r\n                            </div>\r\n                            <div class=\"invi-status-single\">\r\n                                <span>\r\n                                    <img\r\n                                        src=\"/assets/hosthome/mdi_cancel-circle-multiple-outline.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    Declined\r\n                                </span>\r\n                                <span>{{ invitationsdeclined }}</span>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"host-guest-member-cont\">\r\n                        <div\r\n                            class=\"host-guest-member-cont-single\"\r\n                            style=\"margin-bottom: 10px\"\r\n                        >\r\n                            <div>\r\n                                <span></span>\r\n                                <span>\r\n                                    <span>{{ guestcount }}</span>\r\n                                    <span>Total Guests</span>\r\n                                </span>\r\n                            </div>\r\n                            <a\r\n                                :href=\"\r\n                                    route(\r\n                                        'invitedguests.custom',\r\n                                        invitation?.slug ?? ''\r\n                                    )\r\n                                \"\r\n                            >\r\n                                <img\r\n                                    src=\"/assets/miscellenous/user-plus.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                            </a>\r\n                        </div>\r\n                        <div class=\"host-guest-member-cont-single\">\r\n                            <div>\r\n                                <span></span>\r\n                                <span>\r\n                                    <span>{{ members }}</span>\r\n                                    <span>Total Members</span>\r\n                                </span>\r\n                            </div>\r\n                            <a :href=\"route('hostmembers.index')\">\r\n                                <img\r\n                                    src=\"/assets/miscellenous/user-plus.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                            </a>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"host-dash-reports-cont\">\r\n                        <h1\r\n                            @click=\"\r\n                                show == 'reports'\r\n                                    ? (show = 'null')\r\n                                    : (show = 'reports')\r\n                            \"\r\n                        >\r\n                            Reports\r\n                            <img\r\n                                v-if=\"screenWidth <= 631 && show != 'reports'\"\r\n                                src=\"/assets/guestDash/chevron-down.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <img\r\n                                v-if=\"screenWidth <= 631 && show == 'reports'\"\r\n                                src=\"/assets/guestDash/chevron-up.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                        </h1>\r\n                        <div\r\n                            v-show=\"show == 'reports' || screenWidth >= 631\"\r\n                            class=\"host-reports-cont\"\r\n                        >\r\n                            <div class=\"host-reports-head\">\r\n                                <span style=\"width: 50%; padding-left: 46px\"\r\n                                    >Progress</span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 95px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >Accomodation</span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 65px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >Logisitcs</span\r\n                                >\r\n                            </div>\r\n                            <div class=\"host-reports-single\">\r\n                                <span style=\"width: 50%; display: flex\">\r\n                                    <img\r\n                                        src=\"/assets/hosthome/Frame5668.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    <span>Invitation sent</span></span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 95px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >{{ sentaccomodation }}</span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 65px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >{{ sentlogistics }}</span\r\n                                >\r\n                            </div>\r\n                            <div class=\"host-reports-single\">\r\n                                <span style=\"width: 50%; display: flex\">\r\n                                    <img\r\n                                        src=\"/assets/hosthome/Frame5670.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    <span>Accepted</span></span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 95px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >{{ accaccomodation }}</span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 65px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >{{ acclogisitcs }}</span\r\n                                >\r\n                            </div>\r\n                            <div class=\"host-reports-single\">\r\n                                <span style=\"width: 50%; display: flex\">\r\n                                    <img\r\n                                        src=\"/assets/hosthome/Frame5669.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    <span>Alloted</span></span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 95px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >{{ allaccomodation }}</span\r\n                                >\r\n                                <span\r\n                                    style=\"\r\n                                        width: 25%;\r\n                                        min-width: 65px;\r\n                                        text-align: center;\r\n                                    \"\r\n                                    >{{ alllogisitcs }}</span\r\n                                >\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n                <div class=\"guest-left-not-vendor-cont\">\r\n                    <div class=\"guest-left-noti-cont\">\r\n                        <div>\r\n                            <h1\r\n                                @click=\"\r\n                                    show == 'notifications'\r\n                                        ? (show = 'null')\r\n                                        : (show = 'notifications')\r\n                                \"\r\n                            >\r\n                                notifications\r\n                                <img\r\n                                    v-if=\"\r\n                                        screenWidth <= 631 &&\r\n                                        show != 'notifications'\r\n                                    \"\r\n                                    src=\"/assets/guestDash/chevron-down.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                                <img\r\n                                    v-if=\"\r\n                                        screenWidth <= 631 &&\r\n                                        show == 'notifications'\r\n                                    \"\r\n                                    src=\"/assets/guestDash/chevron-up.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                            </h1>\r\n                            <div\r\n                                v-show=\"\r\n                                    (show == 'notifications' ||\r\n                                        screenWidth >= 631) &&\r\n                                    notifications.length > 0\r\n                                \"\r\n                                class=\"noti-all-cont\"\r\n                                :class=\"{ 'increase-height': increaseHeight }\"\r\n                            >\r\n                                <div\r\n                                    v-for=\"(item, index) in notifications\"\r\n                                    :key=\"index\"\r\n                                    class=\"noti-all-single\"\r\n                                >\r\n                                    <img\r\n                                        src=\"/assets/guestDash/Ellipse.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    <span>{{ item.data.status }}</span>\r\n                                </div>\r\n                            </div>\r\n                            <div\r\n                                v-show=\"\r\n                                    (show == 'notifications' ||\r\n                                        screenWidth >= 631) &&\r\n                                    notifications.length == 0\r\n                                \"\r\n                                class=\"no-noti-cont\"\r\n                            >\r\n                                <img\r\n                                    src=\"/assets/hosthome/Frame5672.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                                <h4>No Notifications</h4>\r\n                            </div>\r\n                        </div>\r\n                        <div\r\n                            v-if=\"\r\n                                increaseHeight == false &&\r\n                                notifications.length > 0 &&\r\n                                (show == 'notifications' || screenWidth >= 631)\r\n                            \"\r\n                            @click=\"increaseHeight = true\"\r\n                            class=\"guest-noti-view-more\"\r\n                        >\r\n                            View More\r\n                        </div>\r\n                        <div\r\n                            v-if=\"\r\n                                increaseHeight == true &&\r\n                                notifications.length > 0 &&\r\n                                (show == 'notifications' || screenWidth >= 631)\r\n                            \"\r\n                            @click=\"increaseHeight = false\"\r\n                            class=\"guest-noti-view-more\"\r\n                        >\r\n                            View Less\r\n                        </div>\r\n                    </div>\r\n                    <div v-if=\"screenWidth > 631\" class=\"host-explore-vendor\">\r\n                        <img src=\"/assets/hosthome/vendor.svg\" alt=\"\" />\r\n                        <h3>Explore vendors near you</h3>\r\n                        <p>starting from ₹2000 for couple plan book now</p>\r\n                        <a href=\"/directory\">Explore Vendors</a>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <div class=\"host-home-right-cont\">\r\n            <div\r\n                v-if=\"screenWidth > 631 && todaySchedule.length > 0\"\r\n                class=\"host-today-task\"\r\n            >\r\n                <h1>Today’s Task</h1>\r\n                <div>\r\n                    <div\r\n                        v-for=\"item in todaySchedule\"\r\n                        :key=\"item.id\"\r\n                        class=\"host-today-task-single\"\r\n                    >\r\n                        <div class=\"host-today-task-single-head\">\r\n                            <span>\r\n                                <span></span>\r\n                                {{ item.taskStatus }}\r\n                            </span>\r\n                            <span>{{ item.category.name }}</span>\r\n                        </div>\r\n                        <div class=\"host-today-task-assign\">\r\n                            Assigned to\r\n                            <span>: {{ item.assignable.name }}</span>\r\n                        </div>\r\n                        <p>\r\n                            {{ item.title }}\r\n                        </p>\r\n                        <div class=\"host-today-schedule\">\r\n                            <span\r\n                                ><img\r\n                                    src=\"/assets/icon-calendar.svg\"\r\n                                    alt=\"\"\r\n                                />{{ item.startDate | moment(\"Do MMMM, YYYY\") }}\r\n                                -\r\n                                {{\r\n                                    item.completionDate\r\n                                        | moment(\"Do MMMM, YYYY\")\r\n                                }}</span\r\n                            >\r\n                            <span>\r\n                                <img src=\"/assets/icon-clock.svg\" alt=\"\" />{{\r\n                                    new Date(\"1970-01-01T\" + item.startTime)\r\n                                        | moment(\"h:mm a\")\r\n                                }}\r\n                                -\r\n                                {{\r\n                                    new Date(\r\n                                        \"1970-01-01T\" + item.completionTime\r\n                                    ) | moment(\"h:mm a\")\r\n                                }}</span\r\n                            >\r\n                            <span\r\n                                ><img src=\"/assets/tag.svg\" alt=\"\" />{{\r\n                                    item.taskPriority\r\n                                }}\r\n                                Priority</span\r\n                            >\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"host-cal-events-cont\">\r\n                <div\r\n                    @click=\"\r\n                        show == 'upcomingEvents'\r\n                            ? (show = 'null')\r\n                            : (show = 'upcomingEvents')\r\n                    \"\r\n                    class=\"host-cal-head\"\r\n                >\r\n                    <div>\r\n                        <div>Upcoming tasks</div>\r\n                        <div>Don’t miss the upcoming tasks</div>\r\n                    </div>\r\n                    <a\r\n                        v-if=\"screenWidth > 631\"\r\n                        :href=\"route('hosttasks.index')\"\r\n                    >\r\n                        +\r\n                    </a>\r\n                    <img\r\n                        v-if=\"screenWidth <= 631 && show != 'inviStatus'\"\r\n                        src=\"/assets/guestDash/chevron-down.svg\"\r\n                        alt=\"\"\r\n                    />\r\n                    <img\r\n                        v-if=\"screenWidth <= 631 && show == 'inviStatus'\"\r\n                        src=\"/assets/guestDash/chevron-up.svg\"\r\n                        alt=\"\"\r\n                    />\r\n                </div>\r\n                <div v-show=\"show == 'upcomingEvents' || screenWidth >= 631\">\r\n                    <div class=\"cal-cont\">\r\n                        <Calendar\r\n                            :attributes=\"attr\"\r\n                            :masks=\"{ title: 'MMMM' }\"\r\n                            @dayclick=\"dayclicked\"\r\n                        >\r\n                            <template #day-popover=\"{ dayTitle }\">\r\n                                <div\r\n                                    class=\"text-xs text-gray-300 font-semibold text-center\"\r\n                                >\r\n                                    {{ dayTitle }}\r\n                                </div>\r\n                                <div\r\n                                    v-for=\"(item, index) in todos\"\r\n                                    :key=\"index\"\r\n                                >\r\n                                    <div\r\n                                        v-if=\"\r\n                                            `'` +\r\n                                                new Date(item.startDate) +\r\n                                                `'` ==\r\n                                            `'` + new Date(date.id) + `'`\r\n                                        \"\r\n                                    >\r\n                                        <div>\r\n                                            {{ item.title }}\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                            </template>\r\n                        </Calendar>\r\n                    </div>\r\n                    <div v-if=\"tasks.length > 0\" class=\"host-all-events\">\r\n                        <div\r\n                            v-for=\"(item, index) in tasks\"\r\n                            :key=\"index\"\r\n                            class=\"host-today-task-single\"\r\n                        >\r\n                            <div class=\"host-today-task-single-head\">\r\n                                <span class=\"event-cal\">\r\n                                    <span\r\n                                        style=\"background: #f4a545 !important\"\r\n                                    ></span>\r\n                                    {{\r\n                                        item.startDate | moment(\"Do MMMM, YYYY\")\r\n                                    }}\r\n                                </span>\r\n                                <span class=\"event-time\">{{\r\n                                    new Date(\"1970-01-01T\" + item.startTime)\r\n                                        | moment(\"h:mm a\")\r\n                                }}</span>\r\n                                <span>{{ item.category.name }}</span>\r\n                            </div>\r\n                            <div class=\"host-today-task-assign\">\r\n                                Assigned to\r\n                                <span>: {{ item.assignable.name }}</span>\r\n                            </div>\r\n                            <p>\r\n                                {{ item.title }}\r\n                            </p>\r\n                            <div\r\n                                class=\"host-today-schedule\"\r\n                                style=\"justify-content: flex-end\"\r\n                            >\r\n                                <span\r\n                                    ><img src=\"/assets/tag.svg\" alt=\"\" />{{\r\n                                        item.taskPriority\r\n                                    }}\r\n                                    Priority</span\r\n                                >\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <div\r\n            v-if=\"screenWidth < 631\"\r\n            class=\"host-explore-vendor\"\r\n            style=\"margin: auto\"\r\n        >\r\n            <img src=\"/assets/hosthome/vendor.svg\" alt=\"\" />\r\n            <h3>Explore vendors near you</h3>\r\n            <p>starting from ₹2000 for couple plan book now</p>\r\n            <a href=\"/directory\">Explore Vendors</a>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport Calendar from \"v-calendar/lib/components/calendar.umd\";\r\n\r\nexport default {\r\n    components: {\r\n        Calendar,\r\n    },\r\n    props: [\r\n        \"loggedIn\",\r\n        \"invitation\",\r\n        \"invitationsaccepted\",\r\n        \"invitationsdeclined\",\r\n        \"invitationspending\",\r\n        \"notifications\",\r\n        \"invitationcount\",\r\n        \"accaccomodation\",\r\n        \"allaccomodation\",\r\n        \"sentaccomodation\",\r\n        \"sentlogistics\",\r\n        \"acclogisitcs\",\r\n        \"alllogisitcs\",\r\n        \"guestcount\",\r\n        \"members\",\r\n        \"tasks\",\r\n    ],\r\n    data() {\r\n        return {\r\n            todos: this.tasks,\r\n            screenWidth: window.screen.width,\r\n            increaseHeight: false,\r\n            show: null,\r\n            weekDays: [\r\n                \"Sunday\",\r\n                \"Monday\",\r\n                \"Tuesday\",\r\n                \"Wednesday\",\r\n                \"Thursday\",\r\n                \"Friday\",\r\n                \"Saturday\",\r\n            ],\r\n            months: [\r\n                \"January\",\r\n                \"February\",\r\n                \"March\",\r\n                \"April\",\r\n                \"May\",\r\n                \"June\",\r\n                \"July\",\r\n                \"August\",\r\n                \"September\",\r\n                \"October\",\r\n                \"November\",\r\n                \"December\",\r\n            ],\r\n            date: {\r\n                name: \"\",\r\n                id: \"\",\r\n                day: new Date().getDate(),\r\n                week: new Date().getDay(),\r\n                year: new Date().getFullYear(),\r\n                month: new Date().getMonth(),\r\n            },\r\n        };\r\n    },\r\n    methods: {\r\n        dayclicked(day) {\r\n            this.date.id = day.id;\r\n            this.date.day = day.day;\r\n            this.date.month = day.month - 1;\r\n            this.date.year = day.year;\r\n            this.date.week = day.weekday - 1;\r\n            this.date.name =\r\n                this.weekDays[day.weekday - 1] +\r\n                \" \" +\r\n                this.date.day +\r\n                \", \" +\r\n                this.months[day.month - 1] +\r\n                \" \" +\r\n                this.date.year;\r\n        },\r\n        setWidth() {\r\n            this.screenWidth = window.screen.width;\r\n        },\r\n        countDown() {\r\n            const fixedDate = this.invitation.startDate;\r\n            //console.log(fixedDate);\r\n            const deadlineTimer = document.querySelectorAll(\r\n                \".guest-banner-timer-single > span:nth-child(1)\"\r\n            );\r\n\r\n            const futureDate = new Date(fixedDate);\r\n\r\n            const futureTime = futureDate.getTime();\r\n\r\n            function getRemainingTime() {\r\n                let today = new Date().getTime();\r\n\r\n                let totalRemainingMS = futureTime - today;\r\n                if (totalRemainingMS < 0) {\r\n                    clearInterval(countdown);\r\n                    return;\r\n                }\r\n\r\n                //values in ms\r\n                const Oneday = 24 * 60 * 60 * 1000;\r\n                const Onehour = 60 * 60 * 1000;\r\n                const Oneminutes = 60 * 1000;\r\n\r\n                //remaining time calulation\r\n                let Day = Math.floor(totalRemainingMS / Oneday);\r\n                let Hour = Math.floor((totalRemainingMS % Oneday) / Onehour);\r\n                let Minutes = Math.floor(\r\n                    (totalRemainingMS % Onehour) / Oneminutes\r\n                );\r\n                let Seconds = Math.floor(\r\n                    (totalRemainingMS % Oneminutes) / 1000\r\n                );\r\n\r\n                const value = [Day, Hour, Minutes, Seconds];\r\n\r\n                function format(item) {\r\n                    if (item < 10) {\r\n                        return (item = `0${item}`);\r\n                    } else {\r\n                        return item;\r\n                    }\r\n                }\r\n                deadlineTimer.forEach(function (item, index) {\r\n                    item.innerHTML = format(value[index]);\r\n                });\r\n            }\r\n            let countdown = setInterval(getRemainingTime, 1000);\r\n            getRemainingTime();\r\n        },\r\n    },\r\n    computed: {\r\n        attr() {\r\n            return [\r\n                // Attributes for todos\r\n                ...this.todos.map((todo) => ({\r\n                    dates: todo.startDate,\r\n                    highlight: {\r\n                        style: {\r\n                            backgroundColor: \"#C4456F\",\r\n                        },\r\n                        contentStyle: {\r\n                            color: \"#FFFFFF\",\r\n                        },\r\n                    },\r\n                    // popover: true,\r\n                    popover: {\r\n                        isInteractive: true,\r\n                        visibility: \"click\",\r\n                    },\r\n                    customData: todo,\r\n                })),\r\n                // {\r\n                //     key: \"today\",\r\n                //     highlight: {\r\n                //         style: {\r\n                //             backgroundColor: \"#EB5757\",\r\n                //         },\r\n                //     },\r\n                //     dates: new Date(),\r\n                // },\r\n            ];\r\n        },\r\n        todaySchedule() {\r\n            let todayevents = [];\r\n            let today = new Date().toDateString();\r\n            this.todos.forEach((item) => {\r\n                if (\r\n                    '\"' + new Date(item.startDate).toDateString() + '\"' ==\r\n                    '\"' + today + '\"'\r\n                ) {\r\n                    todayevents.push(item);\r\n                }\r\n            });\r\n            return todayevents;\r\n        },\r\n    },\r\n    mounted() {\r\n        this.$nextTick(() => {\r\n            window.addEventListener(\"resize\", this.setWidth);\r\n            this.countDown();\r\n        });\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.host-home-main-cont {\r\n    display: flex;\r\n    flex-wrap: nowrap;\r\n    background: #ffffff;\r\n}\r\n.host-home-left-cont {\r\n    width: 70%;\r\n    background: #